















































































import {Component, Prop, Vue} from "vue-property-decorator";
import Item from "@/components/Item.vue";
import {CommandInterface} from "@/interfaces/command.interface";

@Component
export default class CommandForm extends Vue {
    private hours = [11, 12, 13, 14, 18, 19, 20, 21, 22];
    private minutes = [0, 5, 10, 15, 20, 25, 30, 35, 40, 45, 50, 55];

    private hour = 11;
    private minute = 0;
    private description = '';


    private command: CommandInterface = null;

    created() {
        this.command = this.$store.state.pendingsCommand;
    }

    onIncreaseItem(item: any) {
        this.$store.commit('addToPendingCommand', item);
    }

    onDecreaseItem(item: any) {
        this.$store.commit('decreasePendingCommandItem', item);
    }

    onValidate() {
        this.$store.commit('addPendingCommandToCommand', {
            hour: this.hour,
            minute: this.minute,
            description: this.description,
            itemsDesc: this.$store.state.pendingsCommand.itemsDesc,
        });
    }

    onCancel() {
        this.$store.commit('clearPendingCommand');
    }
}
