






















import { Component, Prop, Vue } from 'vue-property-decorator';
import ItemDetailLine from './ItemDetailLine.vue';
import {CommandInterface} from "@/interfaces/command.interface";

@Component({
    components: {ItemDetailLine}
})
export default class CommandDetail extends Vue {
    @Prop() private command!: CommandInterface;


    onEdit() {
            this.$store.commit('addCommandToPendingCommand', this.command);
    }

    onDone() {
        this.$store.commit('doneCommand', this.command);
    }
}
